import React, { Component } from "react";
import Header from "./components/Header";
import Images from "./components/Images";
import Image from "./components/Image";
import "./css/app.css";
import "./css/image.css";
import store from "./store";
import "bootstrap/dist/css/bootstrap.css";
import { Provider } from "react-redux";
import { Route, BrowserRouter, Switch } from 'react-router-dom'
import firebase from 'firebase/app';
import 'firebase/messaging';
import { initializeFirebase } from './push-notification'

class App extends Component {
  componentDidMount() {
    initializeFirebase();
    this.notificationListener = firebase.messaging().onMessage((message) => {
      console.log('here');
      console.log(message);
     
      if (message.notification!==undefined && 'Notification' in window) {
        let notificationTitle = message.notification.title
        const notificationOptions = {
          body: message.notification.body,
          icon: message.notification.icon,
          click_action: message.notification.click_action,
          //  badge: message.notification.badge,
          //  tag: 'Lexus-push-notification',
          //  data: {
          //   url: 'https://developers.google.com/web/fundamentals/getting-started/push-notifications/',
          // },
        };

        new Notification(notificationTitle, notificationOptions);
      }

    });
  }

  componentWillUnmount() {
    this.notificationListener();
  }

  render() {
    return (
      <Provider store={store}>
        <BrowserRouter>
          <div className="app">
            <div>
              <Header />
            </div>
            <Switch>
              <Route exact path='/nasa_image_search' component={Images}/>
              <Route path='/nasa_image_search/:nasa_id' component={Image} />
            </Switch>
        </div>
        </BrowserRouter>
      </Provider>
    );
  }
}

export default App;

